// actions call reducers

let nextId = 0

// calling reducers
export const addTodo = (text) => ({
    type: 'ADD_TODO',
    id: nextId++,
    text
})

export const deleteTodo = (id) => ({
    type: 'DELETE_TODO',
    id
})

export const toggleTodo = (id) => ({
    type: 'TOGGLE_TODO',
    id
})

export const setFilter = (filter) => ({
    type: 'SET_FILTER',
    filter
  })



